@import 'reset.scss';
@import 'variables.scss';

body {
  min-height: 100vh;
  min-width: 100vw;
  // background: $black;
}

.container {
  display: grid;

  grid-template-areas: 'nav side content content';

  grid-template-columns: auto auto 1fr;
  grid-template-rows: 1fr;

  height: 100vh;
  box-shadow: inset 0px 11px 8px -10px #ccc;

  nav {
    grid-area: nav;
    margin-left: 0.5rem;
    background: $lightblue;
    box-shadow: 1px 0px 5px rgba(34, 40, 49, 0.5);
    z-index: 10;
  }

  main {
    grid-area: content;
    background: $lightgrey;
    z-index: -5;
  }

  aside {
    grid-area: side;
    margin-right: 0.5rem;
    background: $lightgrey;
  }

  footer {
    grid-area: footer;
    background: aquamarine;
  }

  nav,
  aside {
    margin: 0;
  }

  @media (max-width: 768px) {
    grid-template-areas:
      'nav'
      'content'
      'side';

    grid-template-columns: 1fr;
    grid-template-rows:
      auto /* Header */
      minmax(75px, auto) /* Nav */
      1fr /* Content */
      minmax(75px, auto); /* Sidebar */
  }
}
